

/* CSS declarations go here */

 .App-logo {
  animation: App-logo-spin infinite 20s linear;
  align-items: center;
  height: 4vmin;
  width:4%;
  float: right;
  bottom: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  height: 100%;
  margin: 0;
}

body {
  height: 100%;
  font-family: sans-serif;
  background-color: #FFFFFF;
/*  background-image: linear-gradient(to bottom, #918e82, #615681 50%); */
  background-attachment: fixed;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 10px;
  font-size: 16px;
}

.full-height {
  height: 800px;
/* background:  cornsilk;*/
}

.container {
  max-width: 400px;
  margin: 0 auto;
  max-height: 100%;
  background: white;
}

header {
  background: #7FFFD4;
/*  background-image: linear-gradient(to bottom, #d0edf5, #e1e5f0 100%); */
  font-size: 3.0em;
  font-weight: bold;
  padding: 15px 5px 5px 15px;
  position: center;
  color: #c86737;
} 

/* Style the header with a grey background and some padding */
.header {
  overflow: hidden;
  background-color: #7FFFD4;;
  padding: 20px 10px;
}

/* Style the header links */
.header a {
  float: left;
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 8px;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */
.header a.logo {
  font-size: 4.0em;
  font-weight: bold;
  color: #c76538;
}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #e9c2af;
  color: black;
}

/* Style the active/current link*/
.header a.active {
  background-color: #c86737;
  color: white;
}

/* Float the link section to the right */
.header-right {
  float: left;
}

/* Add media queries for responsiveness - when the screen is 500px wide or less, stack the links on top of each other */
@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }

}



#login-buttons {
  display: block;
}

/* Place the navbar at the bottom of the page, and make it stick */
.navbar {
  background-color: #7FFFD4;
  overflow: hidden;
  position: fixed;
  bottom: 0;
  text-align: center;
  color: FireBrick;
  width: 100%;
}

h1 {
  font-size: 2.0em;
  margin: 0;
  margin-bottom: 10px;
  display: inline-block;
  margin-right: 1em;
}

form {
  margin-top: 10px;
  margin-bottom: -10px;
  position: relative;
}

.new-task input {
  box-sizing: border-box;
  padding: 10px 0;
  background: transparent;
  border: none;
  width: 100%;
  padding-right: 80px;
  font-size: 1em;
}

.new-task input:focus{
  outline: 0;
}

ul {
  margin: 0;
  padding: 0;
  background: whie;
}

.delete {
  float: right;
  font-weight: bold;
  background: none;
  font-size: 1em;
  border: none;
  position: relative;
}

